body {
    margin:0;
    background-color: rgba(11, 54, 78, 0.4);
    font-family: sans-serif;
}
h1{
    margin: 0px;
    margin-top: 15px;
    color: #385b70;
}

.todo-container {
    width:350px;
    position: absolute;
    margin: auto;
    margin-top: 30px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align:center;
}
.input-sec {
    background-color: rgb(118, 150, 160);
    padding: 18px;
    border-radius: 10px;
}
.input-sec input {
   
    border-radius: 5px;
    border: 1px solid rgb(31, 64, 66);
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    box-shadow:0 4px 6px #0a2c3d7c;
    background-color: rgb(118, 150, 160);
    border: none;
    color: rgb(85, 98, 100);
}
ul{
    
    text-align: left;
    background-color: rgba(137, 152, 160, 0.4);;
    padding: 0px;
    border-radius: 10px;
    

}
ul li{
    display: flex;
    list-style:none;
    background-color: rgb(118, 150, 160);
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 10px;
    color: rgb(76, 106, 121);
    box-shadow:0 4px 6px #0002037c;

}
ul li i{
   
    cursor: pointer;
}
input::placeholder{
    color: rgb(85, 98, 100);
}


.icon{
    text-align: right;
    flex: 1;
    
}